import { onBeforeUnmount } from "@vue/runtime-core";
import { Ref, getCurrentInstance, ref, shallowRef, unref } from "vue";

export function useWatermark(
    // appendEl默认传入body
    appendEl:Ref<HTMLElement | null>=ref(document.body) as Ref<HTMLElement>
){
    type attr={
        font?:string;
        fillStyle?:string;
    }
    

    function createBase64(str:string,attr?:attr){
        const can =document.createElement("canvas")
        // const width=400
        // const height=280;
        const width=350
        const height=300
        // const height=200
        const fontSize=15
        Object.assign(can,{width,height})

        const cans =can.getContext("2d")
        if(cans){
            // cans.rotate((-20*Math.PI)/120)
            cans.font=attr?.font ?? `${fontSize}px Reggae One`
            cans.fillStyle=attr?.fillStyle ?? "rgba(0,0,0,0.12)"
            // cans.fillStyle=attr?.fillStyle ?? "rgba(0,0,0,1)"
            // cans.textAlign="left"
            // cans.textBaseline="middle"
            cans.textBaseline = "top"

            cans.fillText(str,-(str.length*fontSize)/2,0)
            cans.fillText(str,width-(str.length*fontSize)/2,0)
            // cans.textBaseline = "top"
            // cans.fillText(str,(width-str.length*fontSize)/2,height-fontSize)
            // cans.textBaseline = "mi"
            cans.fillText(str,(width-str.length*fontSize)/2,height/2)


        }


        return can.toDataURL("image/png")


    }

    const id="waterId"

    const watermarkEl=shallowRef<HTMLElement>();
    // shallow只有对 .value 的访问是响应式的。

    const createWatermark=(
        str:string,
        attr?:attr
    )=>{
        


        // unref= val = isRef(val) ? val.value : val 
        if(unref(watermarkEl)){
            
            updateWatermark({str,attr})
            return id;
        }
        const div=document.createElement("div")

        // 这里就使用到了watermarkEl
        watermarkEl.value=div

        div.id=id;
        div.style.pointerEvents="none"


        div.style.position="fixed"
        div.style.zIndex="10" //低于导航栏
        
        // 这里的el是appendEL 默认是body
        const el=unref(appendEl)

        //如果appendEl不存在 就直接返回id
        if(!el) return id; 
        

        
        // 创建两个新变量 height和 width 并把el的clientHeight和clientWidth属性赋值给他们
        const {offsetHeight:height,offsetWidth:width}=el;
        
        updateWatermark({str,width,height,attr})
        
        el.appendChild(div)
        return id;
    };

    function updateWatermark(
        options:{
            width?:number,
            height?:number,
            str?:string,
            attr?:attr

        }={}
    ){
        
        const el=unref(watermarkEl)
        if(!el) return;
        
        // 把waterMark Element高度和宽度 设为视口高度
        // 乘以2 为了防止模糊的出现
        const basisScale=2
        if (options.width !==undefined) {
            el.style.width = `${options.width*basisScale}px`;
        }
        if (options.height !== undefined) {
            el.style.height = `${options.height*basisScale}px`;
        }
        if(options.str!==undefined && options.height !== undefined && options.width !==undefined){
            // el.style.background=`url(${createBase64(options.str,options.attr)}) left top repeat`
            el.style.background=`url(${createBase64(options.str,options.attr)}) left top repeat`

            const degrees=-30
            const scale=calculateScale(degrees,options.width,options.height)/basisScale

            el.style.transform = `rotate(${degrees}deg) scale(${scale})` ;

            // el.style.transform = `rotate(${degrees}deg)  scale(${calculateScale(degrees,options.width,options.height)})` ;

            el.style.transformOrigin = 'center center'; 
        }
        function calculateScale(degrees:number,width?:number,height?:number) {
            if(width!==undefined && height!==undefined){
                degrees=Math.abs(degrees)
                const rad=degrees*(Math.PI/180)
                const newWidth=width*Math.cos(rad)+height*Math.sin(rad)
                const newHeight=width*Math.sin(rad)+height*Math.cos(rad)
                const scale=Math.max(newHeight/height,newWidth/width)

                // console.log(scale);
                
                return scale

            }
            return 2
        }
      

    }

    function setWatermark(str:string,attr?:attr){
        createWatermark(str,attr)
        
        
        window.addEventListener("resize",func)
        const instance=getCurrentInstance()

        if(instance){
            onBeforeUnmount(()=>{
                clear()
            })
        }

    }

    const func=function(){
        

        const el=unref(appendEl)
        if(!el) return;

        const {clientHeight:height,clientWidth:width}=el;
        updateWatermark({height,width})
    }

    const clear=()=>{
        const domId=unref(watermarkEl)
        watermarkEl.value=undefined
        const el=unref(appendEl)
        if(!el) return
        domId && el.removeChild(domId)
        window.removeEventListener("resize",func)
        // window.addEventListener("resize",func)
    }

    return {setWatermark,clear}
}